

.menuBar {
    width:100%;
    height:180px;
    background:#002c49;
    color:#fff;
    display:flex;
    align-items: center;
    justify-content: center;
    font-size:4vh;
    font-weight:bold;
}

.subWrapper {
    width:100%;
    height:calc(1920px - 180px);
    position:absolute;
    left:0;
    top:180px;
    background:#fff;
}
.mainNotice {
    width:100%;
    height:200px;
    display:flex;
    align-items: center;
    justify-content: center;
    background:#119891;
    color:#fff;
    font-size:3vh;
    font-weight:bold;
}
.mainButtonBox {
    width:100%;
    box-sizing:border-box;
    padding:60px 0;
    height:calc(1920px - 180px - 200px - 608px);
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.mainButton {
    width:1000px;
    height:250px;
    padding-left:10%;
    margin:0px auto;
    box-sizing:border-box;
    background:#333;
    color:#fff;
    font-size:4.5vh;
    display:flex;
    align-items: center;
    justify-content: space-around;
    font-weight:bold;
    letter-spacing:-3px;
}
.mainButton img{
    width:100px;
    position:absolute;
    left:17%;
}
.bgGreen {
    letter-spacing:34px;
    padding-left:13%;
    background-color:#119891;
}
.bgBlue {
    background-color:#1c78d5;    
}
.bgRed {
    background-color:#e10f17;  
}



.homeShip {
    width:10%;
    padding-bottom:10%;
    background-color:#002c49;
    background-image:url('/public/images/ship.png');
    background-repeat:no-repeat;
    background-size:contain;
    position:absolute;
    left:5%;
    top:2%;
    border:none;
}
.homeButton {
    width:10%;
    padding-bottom:10%;
    background-color:#002c49;
    background-image:url('/public/images/home.png');
    background-repeat:no-repeat;
    background-size:contain;
    position:absolute;
    left:5%;
    top:2%;
    border:none;
}
.tabWrapper {
    width:100%;
    display:flex;
    align-items:center;
    justify-content: baseline;
    background:#84c6cb;
    color:#fff;
}
.tabWrapper .isActive {
    background:#119891;
    color:#fff;
	cursor:default;
}

.roomButtonWrap {
    padding:30px 0;
}
.roomButtonWrap button{
    width:20%;
    padding:30px 0;
    border:#fff 2px solid;
    box-sizing:border-box;
    font-size:30px;
	flex-wrap: wrap;
}
.roomButtonWrap .isActive{
    background:#333;
    color:#fff;
}
.floorImg {
    width:100%;
}
.mapWrapper {
    width:1000px;
    display:block;
    margin:0px auto;
    position:relative;
}
.mapMaker {
    width:80px;
    height:90px;
    background-image:url('/public/images/marker.png');
    background-size:contain;
    background-repeat:no-repeat;
    position:absolute;
    left:0;
    top:0;
    animation: blinker infinite 1s;
    z-index:100;
}
.mapMaker:after {
    content:'';
    width:40px;
    height:10px;
    position:absolute;
    left:20px;
    top:88px;
    border-radius:90%;
    background:rgba(0,0,0,0.5);
}
.mapMakerCondition {
    width:40px;
    height:40px;
    position:absolute;
    left:50%;
    top:10px;
    transform:translate3d(-50%,0,0);
    border-radius:50%;
    background:#fff;
}

.roomCondition {
    width:25px;
    height:25px;
    position:absolute;
    background:#f0f0f0;
    left:10px;
    top:10px;
    border-radius:50%;
}

.tableWrapper {
    width:1000px;
    height:1400px;
    box-sizing:border-box;
    border:#119891 5px solid;
    margin: 30px auto 0 auto;
    padding:0;
    font-size:43px;
}
.watingTable {
    width:100%;
    box-sizing:border-box;
    text-align:center;
    margin:0px auto;
}
.watingTable th:nth-child(1){
    width:70%;
    height:100px;
    vertical-align:middle;
    text-align:center;
    box-sizing:border-box;
    border:#fff 1px solid;
    background:#119891;
    color:#fff;
    font-weight:bold;
}
.watingTable th:nth-child(2){
    background:#119891;
    border:#fff 1px solid;
    vertical-align:middle;
    text-align:center;
    color:#fff;
    font-weight:bold;
}
.watingTable td{
    height:129px;
    vertical-align:middle;
    text-align:center;
}
.watingTable td:not(last-child){
    border-bottom:#d0d0d0 1px solid;
}
.watingTable td:nth-child(1){
    position:relative;
    box-sizing:border-box;
    padding-left:100px;
}
.watingCondition {
    width:45px;
    height:45px;
    position:absolute;
    left:55px;
    top:50%;
    transform:translate3d(0,-50%,0);
    border-radius:50%;
}
.pageButtonWrap {
    width:100%;
    height:110px;
    margin-top:20px;
    display:flex;
    align-items: center;
    justify-content: center;
}
.pageButtonWrap > button{
    height:110px;
    padding:0 40px;
    box-sizing:border-box;
    border:#fff 2px solid;
    background:#002c49;
    color:#fff;
    display:flex;
    align-items: center;
    justify-content: center;
    font-size:40px;
}

.pageButtonWrap > button:first-child,
.pageButtonWrap > button:last-child{
    font-family: '굴림';
    font-weight:bold;
}

.idInputArea {
    width:1000px;
    height:150px;
    margin:50px auto;
    display:block;
    box-sizing:border-box;
    padding:20px;
    font-size:70px;
    font-weight:bold;
    text-align:center;
    border:#1c78d5 10px solid;
}
.idInputNotice {
    width:100%;
    padding:40px;
    font-size:55px;
    line-height:80px;
    font-weight:bold;
    text-align:center;    
    background:#f0f0f0;
}
.keyPadbWrapper {
    width:1000px;
    height:1150px;
    margin: 50px auto;
    display:flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.keyPadbWrapper button{
    width:325px;
    height:275px;
    font-size:100px;
    box-sizing:border-box;
    background:#f5f5f5;
    border:#333 1px solid;
    border-left:#ccc 1px solid;
    border-top:#ccc 1px solid;
}
.keyPadbWrapper button:active{
    border:#ccc 1px solid;
    border-left:#333 1px solid;
    border-top:#333 1px solid;
    background:#e0e0e0;
}
.keyPadbWrapper .cancel {
    font-size:70px;
    background:#bbb;
    color:#fff;
}
.keyPadbWrapper .confirm{
    font-size:70px;
    background:#119891;
    color:#fff;
}

.myName {
    width:100%;
    height:146px;
    background:#eee;
    display:flex;
    align-items:center;
    justify-content: center;
    font-weight:bold;
}

.myTable {
    width:100%;
    box-sizing:border-box;
    text-align:center;
    margin:0px auto;
}


.myTable th {
    height:100px;
    border:#fff 1px solid;
    background:#119891;
    color:#fff;
    font-weight:bold;
    vertical-align:middle;
    text-align:center;
}
.myTable th:nth-child(1),
.myTable th:nth-child(2),
.myTable th:nth-child(3) {
    width:15%;
}
.myTable th:nth-child(4) {
    width:30%;
}
.myTable th:nth-child(5) {
}


.myTable td {
    height:129px;
    vertical-align:middle;
    text-align:center;
    font-size:45px;
    position:relative;
}
.myTable td:not(last-child){
    border-bottom:#d0d0d0 1px solid;
}
.nextMark {
    width:0px;
    height:0px;
    border-left: #1c78d5 40px solid;
    border-top: transparent 25px solid;
    border-bottom: transparent 25px solid;
    position:absolute;
    left:50%;
    top:52%;
    transform:translate3d(-50%,-50%,0);
    animation: blinker infinite 1s;
}


@keyframes blinker {
    0% {
        opacity:0;
    }
    50%{
        opacity:1;
    }
    100%{
        opacity:0;
    }
}